import API from "./API"


class AccountService{
    static async loginUser(user){
        console.log(user);
        try {
           const result= await API().post("/login",user);
           console.log(result.data);
           return result.data;
        } catch (error) {
            console.log(error)
        }
    }
    static async registerUser(user){
        try {
           const result=await API().post("/register",user);
           console.log(result.data);
           return result.data;
        } catch (error) {
            console.log(error)
        }
    }
    static async sessionControl(){
        try {
            const result=await API().get("/session-control");
            console.log(result.data);
            return result.data;
         } catch (error) {
             console.log(error)
         }
    }
    static async logoutUser(){
        try {
            const result=await API().get("/logout");
            console.log(result.data);
            return result.data;
        } catch (error) {
            console.log(error)
        }
    }
}

export default AccountService;