<template>
  <form>
      <div class="form-group">
        <label for="exampleInputEmail1">Category Name</label>
        <input
          type="text"
          class="form-control"
          id="exampleInputEmail1"
          aria-describedby="emailHelp"
          placeholder="Enter Title"
          v-model="category.name"
        />
        <small id="emailHelp" class="form-text text-muted">Category Name İs İmportant</small>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Description</label>
        <textarea
          type="text"
          class="form-control"
          id="exampleInputPassword1"
          placeholder="your idea..."
          v-model="category.description"
        />
      </div>
       <button @click.prevent="addCategory" class="btn btn-primary btn-md">Add Category</button>
  </form>
</template>

<script>
import {mapActions} from "vuex";
export default {
    data(){
        return {
            category:{
                name:"",
                description:""
            }
        }
    },

    methods:{
        ...mapActions(["createCategory"]),
        addCategory(){
            if(this.category.name !="" && this.category.description!=""){
                this.createCategory(this.category).then(()=>{
                      this.$router.push("/admin/categories");
                })
            }
        }
    }
}
</script>

