<template>
  <div class="myClass container">
    <form id="formum" @submit.prevent="addPost" enctype="multipart/form-data">
      <div class="form-group">
        <label for="exampleInputEmail1">Title</label>
        <input
          type="text"
          class="form-control"
          id="exampleInputEmail1"
          aria-describedby="emailHelp"
          placeholder="Enter Title"
          v-model.lazy="post.title"
        />
        <small id="emailHelp" class="form-text text-muted">Post Title.</small>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Description</label>
        <textarea
          cols="30"
          rows="10"
          type="text"
          class="form-control"
          id="exampleInputPassword1"
          placeholder="your idea..."
          v-model.lazy="post.description"
        />
      </div>
      <div class="form-group">
        <label for="category">Category</label>
        <select class="form-control" v-model="post.selectedCategory" multiple>
          <option
            v-for="category in categories"
            :key="category.id"
            :value="category.id"
          >
            {{ category.name }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <div
          class="card-body text-center d-flex align-items-center flex-column"
        >
          <img
            height="128"
            class="img-responsive text-center mb-3"
            accept="image/jpeg"
            :src="
              post.selectedImage == null
                ? `/img/default.png`
                : post.selectedImage
            "
          />
          <input
            ref="file"
            type="file"
            style="display: none"
            @change="onChange"
            class="form-control"
          />
          <button
            class="btn btn-outline-secondary"
            type="button"
            @click="$refs.file.click()"
          >
            Resim Seç
          </button>
        </div>
      </div>
      <button class="btn btn-primary">Submit</button>
    </form>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import PostService from "../../services/PostAPI"
export default {
  data() {
    return {
      post: {
        title: "",
        description: "",
        selectedImage: "",
        file: "",
        selectedCategory: [],
      },
    };
  },
  methods: {
    ...mapActions(["createPost"]),
    ...mapGetters(["getAllCategories","getUser"]),
    onChange(e) {
      //Şimdilik kullanılmıyor
      this.post.file =this.$refs.file.files[0];
      console.log(this.post.file);
      this.post.selectedImage = URL.createObjectURL(this.post.file);
      //this.post.selectedImage = URL.createObjectURL(this.post.file);
      console.log(this.post.selectedImage);
    },
    addPost() {
  
      this.createPost(this.post).then(() => {
        //Burada Bu işlemin sonucunu beklemeden asenkron oldugu için geçer hataya düşer
        this.$router.push("/");
      }); //EKlendikten Sonra Anasayfaya Yönlendirelim Şimdilik
    },
  },
  computed: {
    categories() {
      return this.getAllCategories();
    },
    isUser(){
      return this.getUser();
    }

  },
  created(){
   if(!this.isUser.isAuthenticated){
     this.$router.push("/login")
   }
  }
};
</script>

<style scoped>
  .myClass{
    padding-top: 10%;
    padding-bottom: 5%;
  }
</style>