import API from "./API"

class PostService {
    static async getAllPost() {
        try {
            const result = await API().get("/")
            // console.log(result.data.posts)
            return result.data.posts;
        } catch (error) {
            console.log(error)
        }
    }

    static async getByName(id) {
        try {
            console.log(id)
            const result = await API().get("/posts/" + id)

            return result.data.post;
        } catch (error) {
            console.log(error)
        }
    }
    static async createPost(post) {
        console.log(post)
        try {
            const result =await API().post("/create-post",post);
            return result.data;
        } catch (error) {
            console.log(error)
        }
    }
    static async getCategoryByProduct(categoryId) {
        console.log("BUrada")
        try {
            const posts = await API().get("/categories/" + categoryId);
            console.log(posts.data.posts);
            return posts.data.posts;
        } catch (error) {
            console.log(error)
        }
    }
    static async getMyPost(userId){
        try {
            const posts = await API().get("/my-post/" + userId);
            console.log(posts.data);
            return posts.data;
        } catch (error) {
            console.log(error)
        }
    }
    static async deletePost(postId){
        try {
            const result=await API().post("/admin/delete-post/"+postId);
            console.log(result);
        } catch (error) {
            console.log(error)
        }
    }

}

export default PostService;