<template>
  <div class="myClass">
    <div v-if="posts.length > 0" class="table-responsive ">
      <table class="table">
        <thead class="thead-dark">
          <tr>
            <th scope="col">#</th>
            <th scope="col">ID</th>
            <th scope="col">Title</th>
            <th scope="col">User</th>
          </tr>
        </thead>
        <tbody v-for="post in posts" :key="post.id">
          <tr>
            <th scope="row">#</th>
            <td>{{ post.id }}</td>
            <td>{{ post.title }}</td>
            <button @click="deletePostt(post.id)" class="btn btn-danger btn-sm m-2">Delete Post</button>
            <button class="btn btn-info btn-sm m-2">Edit Post</button>
          </tr>
        </tbody>
      </table>
    </div>
    <div
      v-else
      class="card text-white bg-warning mb-3 mx-auto p-4"
      style="max-width: 50rem"
    >
  
      <div class="card-body">
        <h5 class="card-title">Uyarı</h5>
        <p class="card-text">
         Eklemiş Olduğunuz Herhangi Bir İçerik Yok :(
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import PostService from "../../services/PostAPI";

export default {
  methods: {
    ...mapActions(["getMyPostActions","deletePost"]),
    ...mapGetters(["getMyPost", "getUser"]),
     deletePostt(postId){
      this.$confirm("Silmek İstediğinizden Emin misiniz ?","Uyarı","error").then(data=>{
        if(data){
            this.deletePost(postId);
        }
      })
     
    }
  },

  computed: {
    posts() {
      return this.getMyPost();
    },
    isUser() {
      return this.getUser();
    },
  },

  created() {
    if (!this.isUser.isAuthenticated) {
      return this.$router.push("/login");
    }
    this.getMyPostActions(this.isUser.id);
  },
};
</script>

<style scoped>
.myClass {
  padding-top: 15%;
  padding-bottom: 20%;
}
</style>