import AccountService from "../../services/AccountAPI";
import {router} from "../../router/router"
import UserService from "../../services/UserAPI";
const state = {
    user: {
        name: "",
        email: "",
        password: 0,
        isAuthenticated: false,
        isAdmin: false
    },
    users:[],
    posts:[],
    categories:[],
};
const getters = {
    getUser(state) {
        return state.user;
    },
    getAllUser(state){
        return state.users;
    },
    getPosts(state){
        return state.posts;
    },
    getCategories(state){
        return state.categories;
    }
};
const mutations = {
    setUser(state, value) {
        state.user = value
    },
    setAllUser(state,value){
        state.users=value;
    },
    setPosts(state,value){
        state.posts=value;
    },
    setCategories(state,value){
        state.categories=value;
    },
};
const actions = {
    initAuth({commit,dispatch}){
        let user=JSON.parse(localStorage.getItem('user'));
        if(user){
            commit("setUser",user)
        }
    },
    loginUser({ commit, state }, user) {
        AccountService.loginUser(user).then((data) => {
            if (data.isAuthenticated) {
                const userr = {
                    ...data.user,
                    isAuthenticated: data.isAuthenticated,
                    isAdmin: data.isAdmin,
                }
                commit("setUser", userr)
                localStorage.setItem("user",JSON.stringify(state.user));
            }

            return data;
        })
    },
    registerUser({ commit }, user) {
        AccountService.registerUser(user).then(data => {
            router.push("/login")
        })
    },
    logout({ commit, state }) {
   
        AccountService.logoutUser(state.user).then(data => {
            let user = {
                name: "",
                email: "",
                password: 0,
                isAuthenticated: false,
                isAdmin: false
            }

            localStorage.removeItem("user")
            commit("setUser", user)
            router.push("/")
            return data;
        })

        

    },
    getAllUserActions({commit,dispatch}){
        UserService.getAllUser().then(data=>{
            commit("setAllUser",data);
        })
    },
    getAllPostAdmin({commit,dispatch}){
        UserService.getAllPost().then(data=>{
            commit("setPosts", data)
        })
    },
    getAllCategoriesAdmin({commit,dispatch}){
        UserService.getAllCategories().then(data=>{
            commit("setCategories",data)
        })
    },
    deleteUser({commit,dispatch},userId){
        UserService.deleteUser(userId).then(data=>{
             dispatch("getAllUserActions")
        });

    },
    

};

export default {
    state,
    getters,
    mutations,
    actions
}