<template>
  <div class="table-responsive">
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">#</th>
          <th scope="col">Name</th>
          <th scope="col">Email</th>
          <th scope="col">Update</th>
        </tr>
      </thead>
      <tbody v-for="user in allUser" :key="user.id">
        <tr>
          <th scope="row">1</th>
          <td>{{ user.name }}</td>
          <td>{{ user.email }}</td>
          <button
            @click.prevent="deleteUserr(user.id)"
            class="btn btn-danger btn-sm m-2"
          >
            Delete User
          </button>
          <button class="btn btn-info btn-sm m-2">Edit User</button>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import mixin from "../../mixins/adminMixin";
export default {
  mixins: [mixin],
  methods: {
    ...mapActions(["getAllUserActions", "deleteUser"]),
    ...mapGetters(["getAllUser"]),
    deleteUserr(userId) {
      if (userId == 1) {
        return;
      }
      this.$confirm(
        "Silmek İstediğinizden Emin misiniz ?",
        "Uyarı",
        "error"
      ).then((data) => {
        if (data) {
          this.deleteUser(userId);
        }
      });
    },
  },
  computed: {
    allUser() {
      return this.getAllUser();
    },
  },
  created() {
    this.getAllUserActions();
  },
};
</script>

