<template>
  <div class="myClass">
    <div class="container pb-3">
      <div class="row">
        <div v-if="posts.length > 0" class="col-lg-8 col-md-10 mx-auto">
          <app-post v-for="post in posts" :key="post.id">
            <router-link tag="a" :to="'/posts/' + post.id">
              <h2 class="post-title">
                {{ post.title }}
              </h2>
              <h3
                class="post-subtitle"
                style="
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  -webkit-box-orient: vertical;
                "
              >
                {{ post.description }}
              </h3>
            </router-link>

            <p class="post-meta">
              Posted by

              <a href="#">{{ post.user.name }}</a>
              {{ post.createdAt }}
            </p>
          </app-post>
        </div>

        <div v-else class="card text-white bg-warning mb-3 mx-auto">
          <h2>Üzgünüz Böyle Bir İçerik Yok</h2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Post from "../Post";
import { mapActions, mapGetters } from "vuex";
export default {
  methods: {
    ...mapActions(["getCategoryByProduct"]),
    ...mapGetters(["getPostByCategory"]),
  },
  components: {
    appPost: Post,
  },
  computed: {
    posts() {
      return this.getPostByCategory();
    },
  },
  created() {
    this.getCategoryByProduct(this.$route.params.id);
  },
};
</script>

<style scoped>
.myClass {
  padding-top: 7%;
  padding-bottom: 2%;
}
</style>