<template>
  <div class="table-responsive">
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">#</th>
          <th scope="col">ID</th>
          <th scope="col">Title</th>
          <th scope="col">UserName</th>
          <th>Update</th>
        </tr>
      </thead>
      <tbody v-for="post in posts" :key="post.id">
        <tr>
          <th scope="row">#</th>
          <td>{{ post.id }}</td>
          <td>{{ post.title }}</td>
          <td>{{ post.user.name }}</td>
          <button @click.prevent="deletePostt(post.id)" class="btn btn-danger btn-sm m-2">Delete Post</button>
          <button class="btn btn-info btn-sm m-2">Edit Post</button>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  methods: {
    ...mapActions(["getAllPostAdmin","deletePost"]),
    ...mapGetters(["getPosts"]),
     deletePostt(postId){
      this.$confirm("Silmek İstediğinizden Emin misiniz ?","Uyarı","error").then(data=>{
        if(data){
            this.deletePost(postId);
        }
      })
     
    }
  },

  computed: {
    posts() {
      return this.getPosts();
    },
  },

  created(){
    this.getAllPostAdmin();
  }

};
</script>

