<template>
  <div class="table-responsive">
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">#</th>
          <th scope="col">First</th>
          <th scope="col">Last</th>
          <th scope="col">Handle</th>
        </tr>
      </thead>
      <tbody v-for="category in categories" :key="category.id">
        <tr>
          <th scope="row">1</th>
          <td>{{ category.name }}</td>
          <td>{{ category.description }}</td>
          <button @click="deleteCategoryy(category.id)" class="btn btn-danger btn-sm m-2">Delete Category</button>
          <button class="btn btn-info btn-sm m-2">Edit Category</button>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters,mapActions } from "vuex";
import mixin from "../../mixins/adminMixin"
export default {
  mixins:[mixin],
  methods: {
    ...mapGetters(["getCategories"]),
    ...mapActions(["getAllCategoriesAdmin","deleteCategory"]),
    deleteCategoryy(catId){
       this.$confirm("Silmek İstediğinizden Emin misiniz ?","Uyarı","error").then(data=>{
        if(data){
            this.deleteCategory(catId);
        }
      })
    }
  },
  computed: {
    categories() {
      return this.getCategories();
    },
  },
  created(){
    this.getAllCategoriesAdmin();
  }
};
</script>

