import { mapGetters } from "vuex";

var mixin = {
    methods: {
        ...mapGetters(["getUser"])
    },
    computed: {
        isUser() {
            return this.getUser();
        }
    },

    created() {
        console.log(this.isUser)
        if (!(this.isUser.isAuthenticated && this.isUser.isAdmin)) {
            this.$router.push("/")
        }
      
    }

}

export default  mixin;