
import PostService from "../../services/PostAPI";
import { router } from "../../router/router"
const state = {
    posts: [],
    post: {},
    myPosts: []
};
const getters = {
    getAllPost(state) {
        console.log(state.posts)
        return state.posts;
    },
    getOnePost(state) {
        console.log(state.post)
        if (state.post == null) {
            router.push("/")
        }
        return state.post;
    },
    getMyPost(state) {
        return state.myPosts;
    }
};
const mutations = {
    setAllPost(state, value) {
        state.posts = value;

    },
    setOnePost(state, value) {
        state.post = value
    },
    addPost(state, value) {
        state.posts.push(value);
    },
    setMyPost(state, value) {
        state.myPosts = value;
    }
};
const actions = {
    getAllPostActions({ commit }) {
        PostService.getAllPost().then(result => {
            commit("setAllPost", result)
        }).catch(err => {
            console.log(err);

        })

    },
    getByName({ commit }, id) {
        PostService.getByName(id).then(result => {
            commit("setOnePost", result);
        })
    },
    createPost({ commit, dispatch }, value) {//Buradan BackENDe eklenip yönlendirme yapılacak

        const data=new FormData();
        data.append("title",value.title);
        data.append("description",value.description);
        data.append("file",value.file)
        var json_arr = JSON.stringify(value.selectedCategory);
        data.append("selectedCategory",json_arr);

        PostService.createPost(data).then((data) => {
            console.log(data);
            // if (data.isAuthenticated?data.isAuthenticated:null) {//Eğer oturum süresi bittiyse çıkart otomatik
            //     return dispatch("logout");
            // }
            dispatch("getAllPostActions");//Tekrar içeriği güncelliyor
        })
    },
    getMyPostActions({ commit, dispatch }, userId) {
        PostService.getMyPost(userId).then((data) => {
            if (data.isAuthenticated == false) {//Eğer oturum süresi bittiyse çıkart otomatik
                return dispatch("logout");
            }
            if (data.posts) {
                commit("setMyPost", data.posts)
            }

        })
    },
    deletePost({ commit, dispatch,state }, postId) {
        PostService.deletePost(postId).then(data => {
            dispatch("getAllPostAdmin")//Admin tarafındaki liste
           //;!!!!! console.log(state)//Bu buradaki statei görüyor biz localstorage üzerinden işlem yapmalıyız
            let user=JSON.parse(localStorage.getItem('user'));
            dispatch("getMyPostActions",user.id)//My Posttdan Silince
            dispatch("getAllPostActions")//Kullanıcı tarafındaki liste aslında tek listede olabilridi !
        });
    }
};

export default {
    state,
    getters,
    mutations,
    actions
}