import Vue from "vue"
import VueRouter from "vue-router"
import Home from "../components/pages/Home"
import PostDetail from "../components/Post-Detail"
import Admin from "../components/pages/Admin"
import PostAdmin from "../components/admin/Post"
import User from "../components/admin/User"
import CategoryPost from "../components/pages/CategoryPost"
import AllPost from "../components/pages/AllPost"
import CreateCategory from "../components/admin/CreateCategory"
import Login from "../components/pages/Login"
import CreatePost from "../components/pages/CreatePost"
import Post from "../components/pages/Post"
import Register from "../components/pages/Register"
import Category from "../components/admin/Category"
Vue.use(VueRouter);

const routes = [
    { path: "/", component: Home },
    { path: "/posts", component: AllPost },
    { path: "/posts/:id", component: PostDetail, name: "getByName" },
    { path: "/categories/:id", component: CategoryPost },
    { path: "/login", component: Login },
    { path: "/register", component: Register },
    { path: "/create-post", component: CreatePost },
    { path: "/my-posts", component: Post },
    {
        path: "/admin",
        children: [
            { path: "posts", component: PostAdmin },
            { path: "users", component: User },
            { path: "categories", component: Category },
            { path: "create-category", component: CreateCategory }
        ],
        components: {
            default: Admin,
        }
    },
    { path: "*", redirect: "/" }
]

export const router = new VueRouter({
    routes: routes,
    mode: "hash"
})