
import CategoryService from "../../services/CategoryAPI";
import PostService from "../../services/PostAPI";
const state={
    categories:[],
    postsByCategory:[],
};
const getters={
  getAllCategories(state){
      //console.log(state.categories)
      return state.categories;
  },
  getPostByCategory(state){
      return state.postsByCategory;
  }
};
const mutations={
    setAllCategories(state,value){
        state.categories=value
    },
    setPostByCategory(state,value){
       
        state.postsByCategory=value
    }
};
const actions={
    getAllCategory({commit}){
        CategoryService.getAllCategories().then(categories=>{
            commit("setAllCategories",categories);
        }).catch(err=>{
            console.log(err);
        })
    },
    getCategoryByProduct({commit},categoryId){
        console.log(categoryId);
        PostService.getCategoryByProduct(categoryId).then(posts=>{
            commit("setPostByCategory",posts);
        }).catch(err=>{
            console.log(err);
        })
    },
    createCategory({commit,dispatch},category){
        CategoryService.createCategory(category).then(()=>{
            dispatch("getAllCategory")
        })
    },
    deleteCategory({ commit, dispatch }, catId) {
        CategoryService.deleteCategory(catId).then(data => {
            dispatch("getAllCategory")//Kullancıı tarafındaki liste
            dispatch("getAllCategoriesAdmin")//Kullanıcı tarafındaki liste aslında tek listede olabilridi !
        });
    }

};

export default{
    state,
    getters,
    mutations,
    actions
}